// Light
@font-face {
    font-family: 'Ubuntu';
    src : url('./Ubuntu-Light.ttf') format("truetype");
    font-weight: 300;
}
@font-face {
    font-family: 'Ubuntu';
    src : url('./Ubuntu-LightItalic.ttf') format("truetype");
    font-weight: 300;
    font-style: italic;
}

